.opnbtn.selected-opnbtn{
  height: 6vh;
  cursor:pointer;
  opacity: 0.95;
  padding: 0px;
  border-width: 3px;
  border-color: #e74242;
  background-color: #e74242;
  color:rgb(247, 247, 247);
  content: 'Close';
}

.opnbtn{
  height: 4vh;
  border-radius: 10px 10px 0 0;
  float: right;
  transition: all ease-out 0.2s;
  border-bottom: 0;
  background-color: rgb(247, 247, 247);
  color: black;
  border-color: black;
  width: 100%;
  border: none;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.4), 0 6px 20px 0 rgba(0, 0, 0, 0.3);
}

.faveList{
  list-style-type: none;
  padding: 0px 0px;
  margin: 0px 30px;
}

.rmvbtn{
  font-weight: bold;
  border-radius: 5px;
  border-style: solid;
  border-color: #e74242;
  color:  #e74242;
  padding: 5px;
  font-size: 12px;
  text-align: center;
  background-color: none;
  transition: 0.2s;
  margin: 7px 0px;
}



@media screen and (min-width: 320px) {
      .nominationItems{
          width: 85vw;
          font-size: calc(12px + 6 * ((100vw - 320px) / 680));
          opacity: 0.95;
      }
      .Nominee{
          align-items: center;
      }
      .opnbtn{
        opacity: 0.7;
    }
  }

  @media screen and (min-width: 1000px) {
    .nominationItems{
        width:25vw;
        font-size: 12px;
        opacity: 0.75;
    }
    .opnbtn, .selected-opnbtn{
        width:25vw;
    }

    .rmvbtn:hover{
      color: rgb(247, 247, 247);
      background-color: #e74242;
      transform: scale(1.1);
      cursor: pointer; 
    }

    .selected-opnbtn:hover span {
      display:none;
    }
    
    .selected-opnbtn:hover:before {
      transition-delay: 0.5;
      content:"Close";
    }
    .opnbtn:hover{
      cursor:pointer;
      opacity: 0.95;
      height: 6vh;
    } 
      
    .selected-opnbtn:hover {
      border-width: 3px;
      border-color: #e74242;
      background-color: #e74242;
      color:rgb(247, 247, 247);
    }

  }



  .nominationItems{
      margin: 0px;
      
      border: none;
      color: #101318;
      background-color: rgb(247, 247, 247);
      box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.4), 0 6px 20px 0 rgba(0, 0, 0, 0.3);
      height: 0px;
      overflow: hidden;
      transition: height ease-out 0.3s;
  }

  .nominationItems.show{
      height:72vh;
  }

  .Nominee{
      display: flex;
      flex-direction: column;
      float: right;
      font-weight: bold; 
      scroll-behavior: auto;
      border-radius: 10px 10px 0 0;
  }

  .divider{
      height: 2px;
      background-color:#52d19c;
      width: 100%;
      margin: 6px 0px 6px 0px;
      align-self: center; 
  }

